* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    background-color: #1b1f27;
}

.container {
    min-height: 100vh;
    height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.display {
    color: #fff;
    font-size: 38px;
    font-weight: 900;
    margin-bottom: 25px;
}

.button {
    cursor: pointer;
    min-width: 80px;
    padding: 10px;
    margin: 10px;
    border: 3px solid;
    border-radius: 18px;
    background: #3882cc;

    color: #fff;
    font-size: 14px;
    text-transform: uppercase;
    outline: none;

    transition: 0.4s;
}

.button:hover {
    background-color: transparent;
    box-shadow: 0 0 5px 0;
}